import React, { useContext } from "react";
import { GlobalContext } from "../../App";

const CurrentPlayer = ({ playerRole }) => {
  const { globalState } = useContext(GlobalContext);
  const { currHitter, currPitcher } = globalState;

  return (
    <div>
      <div>{playerRole}</div>
      <div>
        <span>{playerRole === "Ìà¨Ïàò" ? currPitcher.name : currHitter.name}</span>
        <span>
          {playerRole === "Ìà¨Ïàò"
            ? `#${currPitcher.pitchCount}`
            : `${currHitter.plateAppearances}ÌÉÄÏÑù ${currHitter.hits}ÏïàÌÉÄ`}
        </span>
      </div>
    </div>
  );
};

export default CurrentPlayer;
